'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { QuizEmbed } from '@/components/QuizEmbed';
import { SecurityWarning, SecurityStatus } from '@/components/SecurityWarning';
import { useLockdown } from '@/hooks/useLockdown';
import { useSecurityMonitor, useViolationAlert } from '@/hooks/useSecurityMonitor';
import { isValidSessionId } from '@/lib/security/validator';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';

interface QuizData {
  sessionId: string;
  quizUrl: string;
  createdAt: string;
  teacherId: string;
}

export default function QuizPage() {
  const params = useParams();
  const router = useRouter();
  const sessionId = params.id as string;

  const [quizData, setQuizData] = useState<QuizData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [quizStarted, setQuizStarted] = useState(false);
  const [showWarning, setShowWarning] = useState(false);

  const { 
    isLockdownActive, 
    enableLockdownMode, 
    disableLockdownMode,
    isFullscreen 
  } = useLockdown();

  const { 
    isMonitoring, 
    violations, 
    violationCount, 
    lastViolation,
    startMonitoring, 
    stopMonitoring 
  } = useSecurityMonitor();

  const { shouldAlert } = useViolationAlert(3);

  // Load quiz data on mount
  useEffect(() => {
    const loadQuizData = () => {
      try {
        // Validate session ID format
        if (!isValidSessionId(sessionId)) {
          setError('Invalid session ID format');
          setIsLoading(false);
          return;
        }

        // Load quiz data from localStorage (in a real app, this would be from an API)
        const storedData = localStorage.getItem(`quiz_${sessionId}`);
        if (!storedData) {
          setError('Quiz session not found. Please check your link or contact your teacher.');
          setIsLoading(false);
          return;
        }

        const data: QuizData = JSON.parse(storedData);
        setQuizData(data);
        setIsLoading(false);
      } catch (err) {
        console.error('Error loading quiz data:', err);
        setError('Failed to load quiz data');
        setIsLoading(false);
      }
    };

    loadQuizData();
  }, [sessionId]);

  // Handle security violations
  useEffect(() => {
    if (shouldAlert && lastViolation && quizStarted) {
      setShowWarning(true);
    }
  }, [shouldAlert, lastViolation, quizStarted]);

  // Auto-submit quiz after max violations
  const handleForceSubmit = () => {
    alert('Quiz has been automatically submitted due to security violations.');
    handleExitQuiz();
  };

  const handleStartQuiz = async () => {
    try {
      // Enable security measures
      const lockdownSuccess = await enableLockdownMode();
      const monitoringSuccess = startMonitoring();

      if (!lockdownSuccess || !monitoringSuccess) {
        setError('Failed to enable security measures. Please try again.');
        return;
      }

      setQuizStarted(true);
    } catch (err) {
      console.error('Error starting quiz:', err);
      setError('Failed to start secure quiz mode');
    }
  };

  const handleExitQuiz = () => {
    // Disable security measures
    disableLockdownMode();
    stopMonitoring();
    
    // Redirect to home page
    router.push('/');
  };

  const handleAcknowledgeWarning = () => {
    setShowWarning(false);
  };

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      if (isLockdownActive) {
        disableLockdownMode();
      }
      if (isMonitoring) {
        stopMonitoring();
      }
    };
  }, [isLockdownActive, isMonitoring, disableLockdownMode, stopMonitoring]);

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading quiz session...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 p-4">
        <Card className="max-w-md w-full">
          <CardHeader className="text-center">
            <CardTitle className="text-red-600">Quiz Access Error</CardTitle>
            <CardDescription>{error}</CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <Button onClick={() => router.push('/')} className="w-full">
              Return to Home
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (!quizStarted) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100 p-4">
        <Card className="max-w-2xl w-full">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl text-gray-900">
              Secure Quiz Environment
            </CardTitle>
            <CardDescription>
              Session ID: {sessionId}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="bg-amber-50 border border-amber-200 rounded-lg p-4">
              <h3 className="font-semibold text-amber-800 mb-2">
                ⚠️ Important: Security Measures Will Be Activated
              </h3>
              <p className="text-amber-700 text-sm mb-3">
                When you start the quiz, the following security features will be enabled:
              </p>
              <ul className="text-sm text-amber-700 space-y-1">
                <li>• Fullscreen mode will be enforced</li>
                <li>• Keyboard shortcuts will be disabled</li>
                <li>• Right-click and context menus will be blocked</li>
                <li>• Tab switching will be monitored</li>
                <li>• Window focus changes will be detected</li>
                <li>• Copy/paste functions will be restricted</li>
              </ul>
            </div>

            <div className="bg-red-50 border border-red-200 rounded-lg p-4">
              <h3 className="font-semibold text-red-800 mb-2">
                🚨 Violation Policy
              </h3>
              <ul className="text-sm text-red-700 space-y-1">
                <li>• You are allowed a maximum of 3 security violations</li>
                <li>• Each violation will trigger a warning dialog</li>
                <li>• After 3 violations, your quiz will be automatically submitted</li>
                <li>• All violations are logged with timestamps</li>
              </ul>
            </div>

            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
              <h3 className="font-semibold text-blue-800 mb-2">
                ✅ System Requirements
              </h3>
              <ul className="text-sm text-blue-700 space-y-1">
                <li>• Use a modern web browser (Chrome, Firefox, Safari, Edge)</li>
                <li>• Ensure stable internet connection</li>
                <li>• Close all other applications and browser tabs</li>
                <li>• Use a single monitor setup if possible</li>
                <li>• Have your student ID ready if required</li>
              </ul>
            </div>

            <div className="flex space-x-3">
              <Button 
                onClick={handleStartQuiz}
                className="flex-1 bg-green-600 hover:bg-green-700"
              >
                I Understand - Start Secure Quiz
              </Button>
              <Button 
                onClick={handleExitQuiz}
                variant="outline"
                className="flex-1"
              >
                Cancel
              </Button>
            </div>

            <p className="text-xs text-center text-gray-500">
              By starting the quiz, you acknowledge that you understand and agree to the security measures.
            </p>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="relative">
      {/* Security Status Indicator */}
      <SecurityStatus 
        violationCount={violationCount}
        maxViolations={3}
        isMonitoring={isMonitoring}
      />

      {/* Quiz Embed */}
      {quizData && (
        <QuizEmbed
          quizUrl={quizData.quizUrl}
          sessionId={sessionId}
          onError={(error) => setError(error)}
        />
      )}

      {/* Security Warning Dialog */}
      <SecurityWarning
        violation={lastViolation}
        violationCount={violationCount}
        maxViolations={3}
        onAcknowledge={handleAcknowledgeWarning}
        onForceSubmit={handleForceSubmit}
        isOpen={showWarning}
      />

      {/* Emergency Exit (hidden by default, can be accessed via specific key combination) */}
      <div className="fixed bottom-0 left-0 w-1 h-1 opacity-0">
        <Button
          onClick={handleExitQuiz}
          className="text-xs"
          variant="ghost"
        >
          Exit
        </Button>
      </div>
    </div>
  );
}
